import { ConsoleLogger, ToolDefinition, Tool, z } from '@multimodal/mcp-agent';
import { CustomBrowserTool, MCPBrowserTool } from './base';

/**
 * NavigationTools - Provides browser navigation tools
 * 
 * Implements essential navigation capabilities like page navigation,
 * back/forward, refresh, and tab management.
 */
export class NavigationTools extends MCPBrowserTool {
  constructor(logger: ConsoleLogger) {
    super(logger);
    this.category = 'browser';
  }

  getTools(): ToolDefinition[] {
    const tools: ToolDefinition[] = [];
    
    // Navigation tools from MCP Browser
    const mcpTools = [
      { name: 'browser_navigate', description: 'Navigate to a URL' },
      { name: 'browser_go_back', description: 'Go back to the previous page' },
      { name: 'browser_go_forward', description: 'Go forward to the next page' },
      { name: 'browser_tab_list', description: 'List all open tabs' },
      { name: 'browser_new_tab', description: 'Open a new tab' },
      { name: 'browser_close_tab', description: 'Close a tab' },
      { name: 'browser_switch_tab', description: 'Switch to a different tab' },
    ];
    
    // Add MCP tools
    mcpTools.forEach(tool => {
      const toolDef = this.createMCPToolDefinition(tool.name, tool.description);
      if (toolDef) tools.push(toolDef);
    });
    
    return tools;
  }
}
